module.exports = "<pr-search-bar (terms)=\"onTerms($event)\"></pr-search-bar>\r\n<div class=\"ui padded collapsed grid\">\r\n  <div class=\"five column doubling collapsed row\">\r\n    <pr-card class=\"column\" [name]=\"card.name\" [rarity]=\"getRarity(card.rarity)\" [cardId]=\"card.id\"\r\n             [count]=\"getCount(collection | async, card.name, 'norm')\"\r\n             [goldCount]=\"getCount(collection | async, card.name, 'gold')\"\r\n             *ngFor=\"let card of cards | async | prCollectionFilter : (collection | async) : search\"></pr-card>\r\n  </div>\r\n</div>\r\n\r\n<pr-legend>\r\n  <div><p>\r\n    You can use similar search tags\r\n    <a href=\"http://us.battle.net/hearthstone/en/blog/20056284/search-your-hearthstone-collection-like-never-before-3-17-2016\"\r\n       target=\"_blank\">\r\n      as in the Hearthstone client\r\n    </a>\r\n    to filter your collection. Assuming that you are opening Gadgetzan packs you can search for:\r\n  </p>\r\n    <div>\r\n      <ul>\r\n        <li>\r\n          Exact card name\r\n          <ul>\r\n            <li>\r\n              searching for <strong>patches</strong> will show\r\n              <pr-card class=\"inline legend\" [name]=\"'Patches the Pirate'\" [rarity]=\"'lgnd'\" [count]=\"2\" [goldCount]=\"0\"></pr-card>\r\n            </li>\r\n          </ul>\r\n        </li>\r\n        <li>\r\n          Consecutive characters in the card name (fuzzy search)\r\n          <ul>\r\n            <li>\r\n              searching for <strong>mom</strong> will show\r\n              <pr-card class=\"inline legend\" [name]=\"'Mistress of Mixtures'\" [rarity]=\"'comn'\" [count]=\"2\" [goldCount]=\"0\"></pr-card>\r\n            </li>\r\n            <li>\r\n              searching for <strong>vp</strong> will show\r\n              <pr-card class=\"inline legend\" [name]=\"'Volcanic Potion'\" [rarity]=\"'rare'\" [count]=\"2\" [goldCount]=\"0\"></pr-card>\r\n            </li>\r\n          </ul>\r\n        </li>\r\n        <li>\r\n          Rarity\r\n          <ul>\r\n            <li>\r\n              searching for <strong>rare</strong> will show all rare cards\r\n            </li>\r\n            <li>\r\n              searching for <strong>epic common</strong> will show all epic and common cards\r\n            </li>\r\n            <li>\r\n              searching for <strong>lgnd comn</strong> will show all legendary and common cards\r\n            </li>\r\n          </ul>\r\n        </li>\r\n        <li>\r\n          Class\r\n          <ul>\r\n            <li>\r\n              searching for <strong>mage</strong> will show all Mage cards\r\n            </li>\r\n            <li>\r\n              searching for <strong>kabal</strong> will show all Kabal cards\r\n            </li>\r\n            <li>\r\n              searching for <strong>hunter neutral</strong> will show all Hunter and Neutral cards\r\n            </li>\r\n          </ul>\r\n        </li>\r\n        <li>\r\n          Mana\r\n          <ul>\r\n            <li>\r\n              searching for <strong>mana:1</strong> will show all OP one-drops\r\n            </li>\r\n            <li>\r\n              searching for <strong>mana:6+</strong> will show all cards that cost 6 or more mana\r\n            </li>\r\n            <li>\r\n              searching for <strong>mana:1-</strong> will show all cards that cost 1 or less mana\r\n            </li>\r\n            <li>\r\n              searching for <strong>mana:3-4</strong> will show all cards that cost 3 or more but 4 or less mana\r\n            </li>\r\n            <li>\r\n              searching for <strong>mana:2 mana:4</strong> will show all cards that cost 2 or 4 mana\r\n            </li>\r\n          </ul>\r\n        </li>\r\n        <li>\r\n          Owned\r\n          <ul>\r\n            <li>\r\n              searching for <strong>owned:1</strong> will show cards that you have only one of\r\n            </li>\r\n            <li>\r\n              the same ranges can be used as for mana\r\n            </li>\r\n          </ul>\r\n        </li>\r\n        <li>\r\n          Extra\r\n          <ul>\r\n            <li>\r\n              searching for <strong>extra</strong> will show legendaries that you have more than two of and other rarities that you have three of (including golden cards)\r\n            </li>\r\n          </ul>\r\n        </li>\r\n        <li>\r\n          Missing\r\n          <ul>\r\n            <li>\r\n              searching for <strong>missing</strong> will show legendaries that you have no copies of and other rarities that you have less than two of (including golden cards)\r\n            </li>\r\n          </ul>\r\n        </li>\r\n        <li>\r\n          Golden\r\n          <ul>\r\n            <li>\r\n              searching for <strong>golden</strong> will show cards that you have at least one golden copy of\r\n            </li>\r\n          </ul>\r\n        </li>\r\n        <li>\r\n          Combinations\r\n          <ul>\r\n            <li>\r\n              searching for <strong>golden epic</strong> will show epics that you have at least one golden copy of\r\n            </li>\r\n            <li>\r\n              searching for <strong>lgnd mana:2-5</strong> will show legendaries that you have at 2 to 5 copies of\r\n            </li>\r\n            <li>\r\n              searching for <strong>mana comn</strong> will show\r\n              <pr-card class=\"inline legend\" [name]=\"'Red Mana Wyrm'\" [rarity]=\"'comn'\" [count]=\"2\" [goldCount]=\"0\"></pr-card>\r\n            </li>\r\n            <li>\r\n              searching for <strong>owned:2 mana:3 a comn rare golden</strong> will show golden rare and common cards that\r\n              cost 3 mana that you have exactly two of that also have an 'a' in their name\r\n            </li>\r\n            <li>\r\n              go crazy\r\n            </li>\r\n          </ul>\r\n        </li>\r\n      </ul>\r\n    </div></div>\r\n</pr-legend>\r\n";

